import moment from 'moment'
const contractStartBlock = 11116308
const secondperBlock = 10
const incrementforOneDay = (60 / secondperBlock) * 60 * 24

export const cleanData = (data, web3) => {
  const cleanedData = data.map((ele) => {
    return {
      txnEth: ele.returnValues.txnEth,
      blockNumber: ele.blockNumber,
      timeStamp: ele.timeStamp
    }
  })

  const dailyEthData = []
  let txnData = []
  const txnCount = []
  let total = 0
  let count = 1
  for (let i = 0; i < cleanedData.length; i++) {
    const element = cleanedData[i]
    const currentMin = contractStartBlock + incrementforOneDay * (count - 1)
    const currentMax = contractStartBlock + incrementforOneDay * count
    if (element.blockNumber > currentMin && element.blockNumber < currentMax) {
      total += Number(web3.utils.fromWei(element.txnEth, 'ether'))
      txnData.push(element)
    } else if (element.blockNumber > currentMax) {
      txnCount.push({
        transactions: txnData.length,
        timeStamp: moment(element.timeStamp * 1000).format('YYYY-MM-DD')
      })
      txnData = []
      dailyEthData.push({
        total,
        timeStamp: moment(element.timeStamp * 1000).format('YYYY-MM-DD')
      })
      total = 0
      count++
    }
  }
  return { dailyEthData, txnCount }
}

export const mergeTwoData = (volumn, price) => {
  const mergedData = []
  for (let i = 0; i < price.length; i++) {
    const element = price[i]
    const data = {
      ...element,
      ...volumn.find((ele) => ele.timeStamp === element.timeStamp)
    }
    if (!data.total) {
      data.total = 0
    }
    mergedData.push(data)
  }
  return mergedData
}
