import config from '../config'
import web3instance from '../web3'
import notify from '../../utils/notify'
import connector from '../../connector'

const EventEmitter = require('events').EventEmitter
const depositEth = async (web3, amountInEther = 0) => {
  console.log('depositEth')
  return new Promise(async (resolve, reject) => {
    const instance = config.HodlDex(web3)
    const etherInWei = web3.utils.toWei(amountInEther, 'ether')
    const accounts = await web3.eth.getAccounts()
    if (accounts.length === 0) return
    instance.methods
      .depositEth()
      .send({
        from: accounts[0],
        value: etherInWei
      })
      .on('receipt', function (receipt) {
        console.log('receipt', receipt)
        resolve(receipt)
      })
      .on('error', function (err) {
        console.log('error', err)
        reject(err)
      })
  })
}

const depositEthReturnImmediately = async (
  web3,
  amountInEther = 0,
  amountToBuy = 0,
  amountinHODlToBuy = 0,
  t
) => {
  console.log('depositEthReturnImmediately')
  return new Promise(async (resolve, reject) => {
    const instance = config.HodlDex(web3)
    const etherInWei = web3.utils.toWei(amountInEther, 'ether')
    const accounts = await web3.eth.getAccounts()
    if (accounts.length === 0) return
    instance.methods
      .depositEth()
      .send({
        from: accounts[0],
        value: etherInWei
      })
      .on('transactionHash', function (hash) {
        notify.success(
          t('trade.notify.successBuy', {
            hodlc: amountinHODlToBuy
          })
        )
        connector
          .buyHodlC(web3, amountToBuy)
          .then(() => {
            const system = t('system.ethereum.confirmed')
            notify.success(`${system}`)
          })
          .catch(() => {
            notify.error(t('system.ethereum.failed'))
          })
      })
      .on('receipt', function (receipt) {
        console.log('receipt')
        resolve(receipt)
      })
      .on('error', function (err) {
        console.log('error', err)
        reject(err.code)
      })
  })
}
