import React, { Component } from 'react'

import { connect } from 'react-redux'

// Install Toastify
import { ToastContainer } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css'
import {
  getUserBalances,
  getDexData,
  initBlockchainEnvironment,
  getBalanceInOldDex,
  getHODLRate
} from '../redux/actions'
import Router from './Router'
import Navigator from '../components/Navs/Navigator'
import connector from '../connector'
import Navbar from '../components/Navs/Navbar'
import Info from '../components/Navs/bottom'
import config from '../config'

class App extends Component {
  componentWillUnmount() {
    clearInterval(this.myinterval2)
  }

  componentDidMount() {
    // init blockchain environment
    this.props
      .initBlockchainEnvironment()
      .then((data) => {
        const { web3 } = data
        this.props.getBalanceInOldDex(web3)
        connector.getCurrentRate(web3)
        // init
        setTimeout(() => {
          this.props.getHODLRate(web3)
        }, 500)
        try {
          this.myinterval2 = setInterval(() => {
            this.props.getHODLRate(web3)
          }, config.rqstInteval.dexData) // request dex data every 5 minutes
        } catch (e) {
          console.log(e)
        }
      })
      .catch(console.error)
  }

  async UNSAFE_componentWillMount() {
    await connector.getHODLRate(this.props.web3)
  }

  render() {
    return (
      <div>
        <Navigator />
        <Navbar />
        <Router />
        <ToastContainer
          position="bottom-right"
          autoClose={3000}
          hideProgressBar={false}
          newestOnTop={false}
          closeOnClick
          rtl={false}
          pauseOnFocusLoss
          draggable
          pauseOnHover
        />
        <Info />
      </div>
    )
  }
}

const mapStateToProps = ({ blockchain }) => {
  return {
    account: blockchain.account,
    web3: blockchain.web3
  }
}

export default connect(mapStateToProps, {
  getUserBalances,
  getDexData,
  initBlockchainEnvironment,
  getBalanceInOldDex,
  getHODLRate
})(App)
