import { GET_ALL_ORDER, GET_ALL_PRICE, GET_ALL_TXN } from '../types'

const INITIAL_STATE = {
  volume: [{ total: 0, timeStamp: 1 }],
  price: [{ timeStamp: 0, HODOLprice: 0 }],
  txn: [{ count: 0, timeStamp: 0 }]
}

export default (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case GET_ALL_ORDER:
      return { ...state, volume: action.payload }
    case GET_ALL_TXN:
      return { ...state, txn: action.payload }
    case GET_ALL_PRICE:
      return { ...state, price: action.payload }
    default:
      return state
  }
}
