import React, { useState, useEffect, useCallback } from 'react'
import { calculator } from '../../utils'
import _ from 'lodash'
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  Tooltip,
  Legend,
  ResponsiveContainer
} from 'recharts'
import { useTranslation, Trans } from 'react-i18next'
import BigNumber from 'bignumber.js'

const CALCULATOR_MODE = 'holding'
const SimpleCalculator = ({ avgDay, avgTransaction, rates }) => {
  const [amount, setAmount] = useState(10000)
  const [currency, setCurrency] = useState('USD')
  const [length, setLength] = useState(365)
  const [transactions, setTransactions] = useState(100)
  const [result, setResult] = useState(0)
  const [chartData, setChartData] = useState([])
  const [totalGrowth, setTotalGrowth] = useState('0')
  const { t } = useTranslation()
  const getResult = () => {
    const currentDate = new Date()
    if (rates > 0) {
      const result =
        calculator(
          amount,
          length,
          transactions,
          currentDate.valueOf(),
          CALCULATOR_MODE,
          null,
          rates,
          currency
        ) || 0
      const endValueInUSD = result.totalValueAtTheEnd
      const chartData = result.chartData
      const totalGrowth = result.totalGrowth
      console.log('result', result)
      setChartData(chartData)
      setResult(endValueInUSD)
      setTotalGrowth(totalGrowth)
    }
  }
  const callBackGetResult = useCallback(_.debounce(getResult, 1000), [
    amount,
    length,
    transactions,
    currency
  ])
  useEffect(() => {
    callBackGetResult(amount, length, transactions, currency)
  }, [amount, length, transactions, currency])

  return (
    <>
      <div className="info-session">
        <form>
          <div>
            <>
              {t('calculator.simple.form.inputAmount.1')}
              <input
                type="number"
                value={amount}
                onChange={(e) => setAmount(e.target.value)}
              />
              <select
                value={currency}
                onChange={(e) => setCurrency(e.target.value)}
              >
                <option value="USD">USD</option>
                <option value="HODLC">HODLC</option>
              </select>
              {t('calculator.simple.form.inputAmount.2')}
              <input
                type="number"
                min={1}
                value={length}
                onChange={(e) =>
                  setLength(e.target.value ? Number(e.target.value) : '')
                }
              />
              {t('calculator.simple.form.inputAmount.3')}
              <br />
            </>
            {t('calculator.simple.form.aboutTransactionPerDay.1')}
            <div className="ui input">
              <input
                type="number"
                value={transactions}
                onChange={(e) => setTransactions(e.target.value)}
              />
            </div>
            {t('calculator.simple.form.aboutTransactionPerDay.2')}
            <br />
            <small className="notes">
              <Trans
                i18nKey="calculator.simple.form.references"
                values={{ avgTransaction, avgDay }}
                components={{ b: <b /> }}
              />
            </small>
          </div>
        </form>
        <div className="result-text">
          <Trans
            i18nKey="calculator.simple.summary.endAmount"
            values={{
              result:
                result &&
                result.toLocaleString('en-US', {
                  style: 'currency',
                  currency: 'USD'
                })
            }}
            components={{ b: <b /> }}
          />
        </div>
        <div className="result-text">
          <Trans
            i18nKey="calculator.simple.summary.endGrowth"
            values={{
              result:
                totalGrowth &&
                totalGrowth.toLocaleString('en-US', {
                  style: 'currency',
                  currency: 'USD'
                })
            }}
            components={{ b: <b /> }}
          />
        </div>
        <div className="result-text">
          <Trans
            i18nKey="calculator.simple.summary.endPercentageIncrease"
            values={{
              result:
                totalGrowth &&
                amount &&
                BigNumber(totalGrowth).dividedBy(amount).times(100).toFixed(2)
            }}
            components={{ b: <b /> }}
          />
        </div>
      </div>
      <div className="chart-container">
        <ResponsiveContainer>
          <LineChart
            data={chartData}
            margin={{
              top: 10,
              right: 20,
              left: 10,
              bottom: 10
            }}
          >
            <XAxis dataKey="timeLength" />
            <YAxis />
            <Tooltip />
            <Legend />
            <Line
              type="monotone"
              unit="USD"
              dataKey="value"
              strokeWidth="3px"
              stroke="#3a2bac"
              activeDot={{ r: 8 }}
              dot={false}
            />
            <Line
              type="monotone"
              unit="USD"
              dataKey="growth"
              strokeWidth="3px"
              stroke="#00bfc7"
              dot={false}
            />
          </LineChart>
        </ResponsiveContainer>
      </div>
    </>
  )
}

export default SimpleCalculator
